import React, { useContext } from "react";
import PropTypes from 'prop-types';
import { ChessContext } from "../../context/ChessProvider.js";

const PromotionModal = ({ onSelect }) => {
    const pieces = ["q", "r", "b", "n"]; // Available promotion pieces (queen, rook, bishop, knight)
    const { gameState } = useContext(ChessContext)



    return (
        <div className="promotion-modal">
            {pieces.map((piece) => (
                <button className="promotion-button"
                    key={piece} onClick={() => onSelect(gameState.turn === 'w' ? piece.toUpperCase() : piece.toLowerCase())}>
                    <img
                        src={`img/chesspieces/${gameState.turn}${piece.toUpperCase()}.png`}
                        alt={piece}
                        className="promotion-image"
                    />
                </button>
            ))}
        </div>
    );
};

PromotionModal.propTypes = {
    onSelect: PropTypes.func.isRequired
}

export default PromotionModal;