import { bitmaskToSquare, bitmaskToPiece, abs, shiftBit, bitboardToString } from '../../util/bitboards.js'
import { FILE_A_MASK, FILE_H_MASK, WHITE_PAWN_RANK, BLACK_PAWN_RANK, RANK_1_MASK, RANK_8_MASK, storeAttack, clearsCheck, storeMove } from './activity.js'
import { Logger } from '../../util/logger.js'

export function storePawnAttacks(activity) {

    let promotionRank = activity.current.isWhite ? RANK_8_MASK : RANK_1_MASK
    let attackMask = 0n
    if (activity.current.sourceMask & promotionRank) return

    

    for (let direction of activity.current.directions.capture) {
        let targetMask = shiftBit(activity.current.sourceMask, direction)
        if (targetMask === 0n) continue

        storeAttack(activity, targetMask)
        attackMask |= targetMask

        let targetSquare = bitmaskToSquare(targetMask)
        let opponentKing = activity.current.isWhite === true
            ? activity.bitboards['k']
            : activity.bitboards['K']

        if ((targetMask & opponentKing)) {
            activity.Checks[targetSquare].push({
                piece: activity.current.piece,
                source: activity.current.sourceSquare,
                target: targetSquare
            })
        }
    }

    activity.AttacksBySquare[activity.current.sourceSquare] = attackMask
    Logger.debug('Combined attack mask for', activity.current.piece, '\n',
        bitboardToString(activity.AttacksBySquare[activity.current.sourceSquare]))


}
export function storePawnMoves(activity) {

    //do not re-order these arrays - 8n must come before 16n

    let initialRank = activity.current.isWhite ? WHITE_PAWN_RANK : BLACK_PAWN_RANK
    let promotionRank = activity.current.isWhite ? RANK_8_MASK : RANK_1_MASK

    for (let direction of activity.current.directions.capture) {

        if (activity.current.sourceMask & promotionRank) break

        Logger.debug('Got pawn capture direction ', direction,
            'from', activity.current.sourceSquare)

        //targetMask can be out of bounds - check exists
        let targetMask = activity.current.sourceMask << direction
        
        if (!targetMask) {

            Logger.debug('Found no mask with direction ',
                direction,
                'from', activity.current.sourceSquare
            )

            continue
        }
        let targetSquare = bitmaskToSquare(targetMask)

        let targetPiece = bitmaskToPiece(targetMask, activity.bitboards)

        //Prevent file wrapping and forward captures
        if ((activity.current.sourceMask & FILE_A_MASK) && (targetMask & FILE_H_MASK)) continue
        if ((activity.current.sourceMask & FILE_H_MASK) && (targetMask & FILE_A_MASK)) continue


        let move = {
            source: activity.current.sourceSquare,
            target: targetSquare,
            piece: activity.current.piece
        }

        //capturing moves
        if (targetPiece) {

            const isWhiteTarget = targetPiece === targetPiece.toUpperCase();

            //skip friendly fire targets
            if ((activity.current.isWhite && isWhiteTarget) ||
                (!activity.current.isWhite && !isWhiteTarget)) continue

            if (targetMask & promotionRank) {
                move.promotion = targetSquare
            }

            if (clearsCheck(activity, targetMask)) {
                move.capture = targetPiece
                storeMove(activity, move)
            }

        }

        //no target - check for enpassant
        if (!targetPiece && targetSquare === activity.enPassant) {
            Logger.debug(
                'Pawn sees en passant at:',
                targetSquare
            )

            if (clearsCheck(activity, targetMask)) {
                storeMove(activity, move)
            }
        }

    }


    for (let direction of activity.current.directions.move) {

        if (activity.current.sourceMask & promotionRank) break

        Logger.debug('Got pawn move direction ', direction,
            'from', activity.current.sourceSquare)

        //targetMask can be out of bounds - check exists
        let targetMask = activity.current.sourceMask << direction
        if (!targetMask) {

            Logger.debug('Found no mask with direction ',
                direction,
                'from', activity.current.sourceSquare
            )

            continue
        }

        let targetPiece = bitmaskToPiece(targetMask, activity.bitboards)
        let targetSquare = bitmaskToSquare(targetMask)

        if (typeof targetPiece === typeof 'string') {
            Logger.debug('Pawn stopped by ', targetPiece)
            break
        }


        let move = {
            source: activity.current.sourceSquare,
            target: targetSquare,
            piece: activity.current.piece
        }


        //Forward direction finds no blocking pieces
        if (abs(direction) === 8n) {

            if (targetMask & promotionRank) {
                move.promotion = targetSquare
            }

            if (clearsCheck(activity, targetMask)) {
                storeMove(activity, move)
            }
        }

        //check if pawn is on start rank
        if (abs(direction) === 16n && (activity.current.sourceMask & initialRank)) {

            move.enPassant = bitmaskToSquare(activity.current.sourceMask << (direction / 2n))

            Logger.debug(
                'Pawn sees two squares to:',
                targetSquare,
                'looking over: ',
                move.enPassant
            )

            if (clearsCheck(activity, targetMask)) {
                storeMove(activity, move)
            }

        }
    }


}