// Logging utility with collapsible stack trace
export const Logger = (() => {
    let isEnabled = true; // Master toggle for all logs
    const levels = { debug: 0, info: 1, warn: 2, error: 3 }; // Logging levels
    let currentLevel = levels.info; // Default log level

    const getStackTrace = () => {
        const stack = new Error().stack.split("\n");
        return stack.slice(1).join("\n"); // Remove "Error" from the start
    };

    const log = (level, ...args) => {
        if (isEnabled && level >= currentLevel) {
            const levelName = Object.keys(levels).find(key => levels[key] === level);

            console.groupCollapsed(`[${levelName.toUpperCase()}]`, ...args);
            console.log(getStackTrace()); // Print full stack trace inside collapsed group
            console.groupEnd();
        }
    };

    return {
        enable: () => (isEnabled = true),
        disable: () => (isEnabled = false),
        setLevel: (levelName) => {
            if (levels[levelName] !== undefined) {
                currentLevel = levels[levelName];
            }
        },
        debug: (...args) => log(levels.debug, ...args),
        info: (...args) => log(levels.info, ...args),
        warn: (...args) => log(levels.warn, ...args),
        error: (...args) => log(levels.error, ...args),
    };
})();
