import React, { useContext } from "react";
import "../../styles/Chessboard.css"; // Styles for the bar
import { ChessContext } from "../../context/ChessProvider.js";

const InfoPanel = () => {
    const { uiSetters, gameState, handlers } = useContext(ChessContext)

    return (
        <div className="info-panel">

            <div className="info-column">

                <button className="button"
                    onClick={() => {
                        uiSetters.setShowLandingMenu(true)
                        uiSetters.setShowGameUI(false)
                        handlers.handleEndGame()
                }}>End Game</button>

{/*                <div className="text-display">
                    {gameState.displayText}
                </div>*/}

            </div>


        </div>
    );
};


export default InfoPanel;
