import { Logger } from '../../util/logger.js'
import GameState from '../gamestate/GameState.js';
import ActivityManager from '../activity/ActivityManager.js';
import Engine from '../engine/Engine.js';
import { bitmaskToSquare } from '../../util/bitboards.js'
import { scorePosition } from '../evaluator/index.js';

export default class ChessGame {
    constructor() {
        this.id = `${Date.now().toString(36)}-${Math.random().toString(36).substring(2, 6)}`;
        this.defaultFEN = "rnbqkbnr/pppppppp/8/8/8/8/PPPPPPPP/RNBQKBNR w KQkq - 0 1"
        this.testRemoveCheckerFEN = "pppppppp/pppppppp/ppN3pp/pp1KQ1pp/pp1q1Rpp/ppB1P1pp/pppppppp/pppppppp w - - 0 1"
        this.testBlockCheckerFEN = "8/8/2N32/2BQK12/23R2/2qP22/8/8 w - - 0 1"
        this.testStalemateFEN = "8/8/8/k7/1Q6/1Q6/8/8 w - - 0 1"
        this.testPromotionFEN = "8/P7/8/8/8/8/p7/8 w - - 0 1"
        this.testRaycastFEN = "Q6B/3pp3/8/8/8/8/8/R7 w - - 0 1"

        Logger.debug('Creating new ChessGame instance: ', this.id)


    }

    newGame(playerColour, FEN) {

        try {
            //create game state from FEN string, includes serialised piece locations
            this.gameState = new GameState(this.id, FEN || this.defaultFEN)
            this.gameState.playerColour = playerColour
            this.gameState.evaluation = scorePosition(this.gameState.bitboards)

            //generate map of activity for all pieces; possible moves, attacks, checks
            this.activity = new ActivityManager(this.gameState)

            //create instance of engine to play against
            this.engine = new Engine()

            Logger.debug('Returning new game state', this.gameState)

            return this.gameState
        } catch (error) {
            Logger.error('Failed to initialise a new game: ', error)
            return null
        }


    }

    play(move) {

        const validMove = this.#playMove(move)
        if (validMove === false) {
            return this.gameState
        }

        if (validMove === 'promotion') {
            //sending current gamestate back for processing via promotepawn pathway
            return this.gameState
        }

        if (validMove === 'engine-promotion') {

            //random promotion logic, replace with new engine function
            let pieces = this.gameState.turn === 'w' ? ['Q', 'R', 'N', 'B'] : ['q', 'r', 'n', 'b']
            let choice = Math.random() < 0.5 ? pieces.slice(0, 2) : pieces.slice(2)
            choice = Math.random() < 0.5 ? choice[0] : choice[1]
            this.gameState.promotePawn(choice)
            this.activity.generateActivity(this.gameState)
            this.#isGameOver()
            return this.gameState
        }

        //generating new moves and attacks for next turn
        this.gameState.updateTurn()
        this.activity.generateActivity(this.gameState)
        this.#isGameOver()
        return this.gameState

        
    }

    #isGameOver() {

        try {

            let moveCount = this.gameState.turn === 'w'
                ? this.activity.WhiteMoveCount
                : this.activity.BlackMoveCount

            Logger.debug('Movecount', this.gameState.turn, moveCount)

            let king = this.gameState.turn === 'w'
                ? this.gameState.bitboards['K']
                : this.gameState.bitboards['k']

            if (!king) {
                Logger.error('No king in bitboards!')
            }

            let checked = this.activity.Checks[bitmaskToSquare(king)]

            if (checked && checked.length > 0 && moveCount === 0) {
                this.gameState.gameOver = this.gameState.turn === 'w' ? 'Black wins!' : 'White wins!'

            } else if (moveCount === 0) {
                this.gameState.gameOver = 'Draw!'

            }

            return !!this.gameState.gameOver


        } catch (error) {
            Logger.error('Failed to check if gameOver: ', error)
            return null
        }

    }

    getMovesFromSquare(source) {
        let playableMoves = this.gameState.turn === 'w'
            ? this.activity.WhiteMoves[source]
            : this.activity.BlackMoves[source]

        if (!playableMoves) {
            Logger.debug('No moves found for chosen piece.')
            return false
        }

        return playableMoves
    }
    

    promote(piece) {

        //generating new moves and attacks for next 
        this.gameState.promotePawn(piece)
        this.activity.generateActivity(this.gameState)
        this.#isGameOver()
        return this.gameState
    }


    #playMove(movePayload) {

        if (!movePayload || !movePayload.source || !movePayload.target) {
            Logger.warn("Invalid move payload:", movePayload);
            return false;
        }

        try {

            //all logic for player chosen moves
            let { source, target } = movePayload;

            if (source == target) {
                Logger.debug('Piece dropped on source square.')
                return false
            }

            let playableMoves = this.gameState.turn === 'w'
                ? this.activity.WhiteMoves[source]
                : this.activity.BlackMoves[source]

            if (!playableMoves) {
                Logger.debug('No moves found for chosen piece.')
                return false
            }

            //iterate over valid move dict to find current move target
            for (let move of playableMoves) {

                if (move.target === target) {
                    return this.gameState.movePiece(move)
                }
            }

            Logger.warn('Move not found in valid moves', movePayload)
            return false

        } catch (error) {
            Logger.error('playMove error:', error)
            return false
        }




    }

    readGameState() {

        try {
            if (!this.gameState) {
                Logger.warn('Requested gameState, none found.')
                return new Error('No active gameState.')
            }

            Logger.debug('Sending GameState to front end:', this.gameState)
            return this.gameState;


        } catch (error) {
            Logger.error('Error in readGameState.', error)
            return new Error('Failed to readGameState.')
        }


    }

}






