import { bitboardToMaskArray, bitmaskToSquare } from "../../util/bitboards.js"
import { Logger } from "../../util/logger.js"

export default class Engine {
    constructor() {

        Logger.debug('Created Engine!')
    }

    generateMove(gameState, activity) {


        Logger.debug('Engine playing colour: ', gameState.turn)

        let pieceMask = gameState.turn === 'w'
            ? gameState.bitboards.white
            : gameState.bitboards.black

        let playableMoves = gameState.turn === 'w'
            ? activity.get().WhiteMoves
            : activity.get().BlackMoves
        Logger.debug('Engine looking to activity for moves: ', playableMoves)

        if (!playableMoves) {
            return new Error ('stalemate!')
        }

        //collecting moves stored per friendly source square
        let moves = []
        let maskArray = bitboardToMaskArray(pieceMask)
        for (let mask of maskArray) {
            let square = bitmaskToSquare(mask)

            if (playableMoves[square]) {
                for (let move of playableMoves[square]) {
                    moves.push(move)
                }
            }
        }

        return this.#chooseRandom(moves)
    }

    #chooseRandom(moves) {
        //choose a move at random
        let moveIndex = Math.floor(Math.random() * (moves.length));
        return moves[moveIndex]
    }
}