import React from "react";
import "../../styles/Chessboard.css"; // Styles for the bar
import EvaluationBar from "./EvaluationBar.js";
import { ChessContext } from "../../context/ChessProvider.js";
import { useContext } from "react";

const EvaluationPanel = () => {

    const { gameState } = useContext(ChessContext)

    const copyFEN = () => {
        navigator.clipboard.writeText(gameState.FEN).then(() => {
            console.log("Copied FEN to clipboard!");
            alert("Copied FEN to clipboard!"); // Optional feedback
        }).catch((err) => {
            console.error("Failed to copy FEN: ", err);
        });
    };

    const copyPGN = () => {
        navigator.clipboard.writeText(gameState.PGN).then(() => {
            console.log("Copied PGN to clipboard!");
            alert("Copied PGN to clipboard!"); // Optional feedback
        }).catch((err) => {
            console.error("Failed to copy PGN: ", err);
        });
    };

    return (
        <div className="evaluation-panel">

            <div className="evaluation-column">
                <div className="text-display">
                    {gameState.FEN}
                </div>

                <button
                    className="button"
                    onClick={() => {
                        copyFEN()
                }}>Copy FEN</button>
{/*
                <div className="text-display">
                    {gameState.PGN}
                </div>

                <button
                    className="button"
                    onClick={() => {
                        copyPGN()
                }}>Copy PGN</button>*/}

            </div>

            {<EvaluationBar />}


        </div>
    );
};

export default EvaluationPanel;
