import React, {useEffect, useRef, useContext} from 'react';
import '../styles/Chessboard.css';
import { Logger } from '../util/logger.js'
import LandingMenu from '../components/chess/LandingMenu.js';
import PlayMenu from '../components/chess/PlayMenu.js';
import GameUI from '../components/chess/GameUI.js';
import {ChessContext } from "../context/ChessProvider.js";
import GameOverModal from '../components/chess/GameOverModal.js';
 

const ChessPage = () => {

    const { uiStates } = useContext(ChessContext)

    const mounted = useRef(false);
    const mountedDOM = useRef(false);
    const domRef = useRef(null)

    //On mount
    useEffect(() => {

        if (!mounted.current) {
            Logger.debug("ChessPage initialised.");
            mounted.current = true

            Logger.debug(
                'Got uiStates from provider:',
                uiStates
            )

        } else {
            return
        }

    }, []);

    useEffect(() => {

        if (!mountedDOM.current) {
            mountedDOM.current = true

            Logger.debug(
                'ChessPage loaded DOM elements'
            )

        } else {
            return
        }
       
    }, [domRef]);


    return (
        <div className="game-container" ref={domRef}>

            {uiStates.showLandingMenu && <LandingMenu />}

            {uiStates.showPlayMenu && <PlayMenu />}

            {uiStates.showGameUI && <GameUI />}

            {uiStates.showGameOverModal && <GameOverModal /> }

        </div>
    );
};

export default ChessPage;
