import React, { useState, useRef, createContext } from "react";
import * as PropTypes from "prop-types";
import { Logger } from '../util/logger.js'
import ChessGame from "../classes/chess/ChessGame.js";

export const ChessContext = createContext();

const ChessProvider = ({ children }) => {
    const ChessRef = useRef(null)
    const [gameState, setGameState] = useState(null);

    const [showLandingMenu, setShowLandingMenu] = useState(true)
    const [showGameUI, setShowGameUI] = useState(false)
    const [showPlayMenu, setShowPlayMenu] = useState(false)
    const [showGameOverModal, setShowGameOverModal] = useState(false)
    const [showPromotionModal, setShowPromotionModal] = useState(false)

    const [aiEnabled, setAIEnabled] = useState(true)
    const [aiDelay, setAIDelay] = useState(500); // Default 1 second
    const [aiThinking, setAIThinking] = useState(false);

    const uiStates = {
        showLandingMenu,
        showGameUI,
        showPlayMenu,
        showGameOverModal,
        showPromotionModal
    }

    const uiSetters = {
        setShowLandingMenu,
        setShowGameUI,
        setShowPlayMenu,
        setShowGameOverModal,
        setShowPromotionModal
    }


    const handleStart = (colour) => {

        if (!ChessRef.current) {
            Logger.debug('No Chess instance found, creating new: ')
            ChessRef.current = new ChessGame()
        }


        Logger.debug('Player selected', colour === 'w' ? 'white' : 'black')
        ChessRef.current.newGame(colour)// Initialize the game state
        setChessRef(ChessRef.current)

        if (aiEnabled && colour === 'b') {
            handleAIMove()
        }
    }

    const handleMove = (move) => {
        if (!ChessRef.current) return

        if (move.source && move.target) {

            Logger.debug('Playing move:', move)
            let newGameState = ChessRef.current.play(move);
            setChessRef(ChessRef.current)
            if (aiEnabled && newGameState.turn !== newGameState.playerColour) {
                handleAIMove()
            }
            
        }

    };

    const handleAIMove = () => {
        if (!ChessRef.current) return

        Logger.debug('Playing AI move:')

        setAIThinking(true); // Disable player input while AI is thinking

        setTimeout(() => {
            const activity = ChessRef.current.activity
            const gameState = ChessRef.current.readGameState()
            const move = ChessRef.current.engine.generateMove(gameState, activity);
            if (move.source && move.target) {
                Logger.debug('Playing engine move:', move)
                ChessRef.current.play(move);
                setChessRef(ChessRef.current)
            }

            setAIDelay(Math.floor(Math.random() * (1 - 500 + 1)) + 500) //up to 3000-500ms feels ok, 1-500 for testing

            setAIThinking(false); // Re-enable player input after AI moves
        }, aiDelay);
    }

    const handlePromotion = (piece) => {
        setShowPromotionModal(false)
        let newGameState = ChessRef.current.promote(piece)
        setChessRef(ChessRef.current)

        if (aiEnabled && newGameState.turn !== newGameState.playerColour) {
            handleAIMove()
        }
    }

    const handleEndGame = () => {

        Logger.debug('Ending Game, clearing ChessRef.current and gameState')
        ChessRef.current = null
        setGameState(null)
    }


    const setChessRef = (chessInstance) => {
        ChessRef.current = chessInstance;

        Logger.debug('Reading GameState!!')
        setGameState({ ...ChessRef.current.readGameState() }); // Initialize the game state
    };

    const handlers = {
        handleStart,
        handleMove,
        handleAIMove,
        handleEndGame,
        handlePromotion
    }


    return (
        <ChessContext.Provider value={{
            gameState: gameState,
            setGameState: setGameState,
            handlers: handlers,
            uiStates: uiStates,
            uiSetters: uiSetters,
            ChessRef: ChessRef,
            setChessRef: setChessRef,
            aiThinking: aiThinking
        }}>
            {children}
        </ChessContext.Provider>
    );
};

ChessProvider.propTypes = {
    children: PropTypes.object.isRequired
}

export default ChessProvider;
