import { bitmaskToSquare, bitmaskToPiece, squareToBitmask, shiftBit,bitboardToString } from "../../util/bitboards.js";
import { Logger } from "../../util/logger.js";
import { getDirection, storeAttack, storeMove } from "./activity.js";



let whiteQSideCastleMove = {
    source: 'e1',
    target: 'c1',
    piece: 'K',
    castlingRook: {
        source: 'a1',
        target: 'd1',
    },
    keySquares: ['e1', 'd1', 'c1', 'b1']

}

let whiteKSideCastleMove = {
    source: 'e1',
    target: 'g1',
    piece: 'K',
    castlingRook: {
        source: 'h1',
        target: 'f1',
    },
    keySquares: ['e1', 'f1', 'g1']
}

let blackQSideCastleMove = {
    source: 'e8',
    target: 'c8',
    piece: 'k',
    castlingRook: {
        source: 'a8',
        target: 'd8'
    },
    keySquares: ['e8', 'd8', 'c8', 'b8']
}

let blackKSideCastleMove = {
    source: 'e8',
    target: 'g8',
    piece: 'k',
    castlingRook: {
        source: 'h8',
        target: 'f8'
    },
    keySquares: ['e8', 'f8', 'g8']
}

export function storeKingAttacks(activity) {

    let attackMask = 0n
    for (let direction of activity.current.directions) {

        //check move doesn't move out of bounds
        let targetMask = shiftBit(activity.current.sourceMask, direction)
        if (targetMask === 0n) continue;

        storeAttack(activity, targetMask)
        attackMask |= targetMask
    }

    activity.AttacksBySquare[activity.current.sourceSquare] = attackMask
    Logger.debug('Combined attack mask for', activity.current.piece, '\n',
        bitboardToString(activity.AttacksBySquare[activity.current.sourceSquare]))
}

export function storeKingMoves(activity) {

    let opponentAttacks = activity.current.isWhite ? activity.BlackAttacks : activity.WhiteAttacks

    //prevent king moving away on check axis
    //TODO: janky check avoidance, remove after implementing magic bitboards
    let checks = activity.Checks[activity.current.sourceSquare]
    if (checks) {
        for (let check of checks) {

            if (check.piece.toLowerCase() === 'p' ||
                check.piece.toLowerCase() === 'n') continue

            let dir = getDirection(activity.current.sourceMask, squareToBitmask(check.source))
            for (let i = 0; i < activity.current.directions.length - 1; i++) {

                if (activity.current.directions[i] === -dir) {
                    activity.current.directions[i] = 0n
                }
            }
        }
    }

    for (let direction of activity.current.directions) {

        if (direction === 0n) continue



        //check move doesn't move out of bounds
        let targetMask = shiftBit(activity.current.sourceMask, direction)
        if (targetMask === 0n) continue;

        let targetSquare = bitmaskToSquare(targetMask)

        //skipping moves resulting in check
        if (opponentAttacks[targetSquare].length > 0) {
            Logger.debug(activity.current.piece,'filtering move to', targetSquare, opponentAttacks[targetSquare])
            continue
        }

        //Cannot take friendly pieces
        const friendly = activity.current.isWhite === true ? 'white' : 'black'
        if (targetMask & activity.bitboards[friendly]) {
            continue
        }

        let move = {
            source: activity.current.sourceSquare,
            target: targetSquare,
            piece: activity.current.piece
        }

        const notFriendly = activity.current.isWhite === false ? 'white' : 'black'
        if (targetMask & activity.bitboards[notFriendly]) {
            move.capture = bitmaskToPiece(targetMask, activity.bitboards)
        }

        storeMove(activity, move)


    }

    storeCastlingMoves(activity)

}


const checkCastlingSquares = (activity, move) => {

    let count = 0
    let opponentAttacks = activity.current.isWhite ? activity.BlackAttacks : activity.WhiteAttacks
    let friendlyPieces = activity.current.isWhite ? activity.bitboards.white : activity.bitboards.black

    Logger.debug('Found opponent attacks', activity.current.isWhite, opponentAttacks)
    for (let square of move.keySquares) {

        Logger.debug('Checking square', square)

        if (count <= 3 && opponentAttacks[square].length > 0) {
            Logger.debug('Breaking with opponent attack')
            break
        }

        if (square === activity.current.sourceSquare) {
            Logger.debug('King square is OK')
            count++
            continue
        }

        if (friendlyPieces & squareToBitmask(square)) {
            Logger.debug('Breaking on friendly piece')
            break
        }

        count++

    }

    Logger.debug('Key Squares:', move.keySquares, move.keySquares.length, count)

    if (count === move.keySquares.length) {
        return true
    }

}

const storeCastlingMoves = (activity) => {
    if (activity.castling === '-') return

    Logger.debug('Looking to store castling moves.')

    const rights = getCastlingRights(activity)

    Logger.debug('Current rights:', rights)

    if (activity.current.isWhite) {

        if (rights.Q && (checkCastlingSquares(activity, whiteQSideCastleMove))) {
            Logger.debug('Storing Q castle move.')
            storeMove(activity, whiteQSideCastleMove) 
        }

        if (rights.K && (checkCastlingSquares(activity, whiteKSideCastleMove))) {
            Logger.debug('Storing K castle move.')
            storeMove(activity, whiteKSideCastleMove)
        }

    } else {

        if (rights.q && (checkCastlingSquares(activity, blackQSideCastleMove))) {
            Logger.debug('Storing q castle move.')
            storeMove(activity, blackQSideCastleMove)
        }

        if (rights.k && (checkCastlingSquares(activity, blackKSideCastleMove))) {
            Logger.debug('Storing k castle move.')
            storeMove(activity, blackKSideCastleMove)
        }

    }
}

const getCastlingRights = (activity) => {
    return {
        K: activity.castling.includes("K"), // White king-side
        Q: activity.castling.includes("Q"), // White queen-side
        k: activity.castling.includes("k"), // Black king-side
        q: activity.castling.includes("q"), // Black queen-side
    };
};