import { React, useContext, useEffect, useRef } from "react";
import Events from "../../util/events.js";
import { Logger } from "../../util/logger.js"
import { ChessContext } from "../../context/ChessProvider.js";

const PlayMenu = () => {

    const { uiSetters, handlers } = useContext(ChessContext)

    const initialized = useRef(false);

    //On mount
    useEffect(() => {

        if (!initialized.current) {
            Logger.debug("Play menu component initialised.");
            initialized.current = true
        } else {
            return
        }

    }, []);

    return (
        <div className="play-menu">

            <div className="play-row-container">

                <button
                    className="button"
                    onClick={() => {
                        uiSetters.setShowPlayMenu(false)
                        uiSetters.setShowGameUI(true)
                        handlers.handleStart('w')
                }}>
                    White
                </button>
                <button
                    className="button"
                    onClick={() => {
                        uiSetters.setShowPlayMenu(false)
                        uiSetters.setShowGameUI(true)
                        handlers.handleStart(Math.random() < 0.5 ? 'w' : 'b')
                    

                }}>
                    Random
                </button>
                <button
                    className="button"
                    onClick={() => {
                        uiSetters.setShowPlayMenu(false)
                        uiSetters.setShowGameUI(true)
                        handlers.handleStart('b')

                }}>
                    Black
                </button>

            </div>

        </div>
    );
};

export default PlayMenu;