import { abs, bitboardToMaskArray, bitmaskToSquare, shiftBit, squareToBitmask, bitboardToString } from "../../util/bitboards.js";
import { Logger } from "../../util/logger.js";

export const FILE_A_MASK = 0x0101010101010101n; // File A (left edge)
export const FILE_H_MASK = 0x8080808080808080n; // File H (right edge)
export const RANK_1_MASK = 0x00000000000000FFn; // Rank 1 (bottom edge)
export const RANK_8_MASK = 0xFF00000000000000n; // Rank 8 (top edge)

export const WHITE_PAWN_RANK = 0x000000000000FF00n
export const BLACK_PAWN_RANK = 0x00FF000000000000n



export function moveDict() {

    const ranks = [1, 2, 3, 4, 5, 6, 7, 8];
    const files = ["a", "b", "c", "d", "e", "f", "g", "h"];
    let dict = {}

    files.forEach((file) => {
        ranks.forEach((rank) => {
            const squareLabel = `${file}${rank}`;
            dict[squareLabel] = []; // Initialize with an empty array
        });
    });

    return dict

}

export function maskDict() {

    const ranks = [1, 2, 3, 4, 5, 6, 7, 8];
    const files = ["a", "b", "c", "d", "e", "f", "g", "h"];
    let dict = {}

    files.forEach((file) => {
        ranks.forEach((rank) => {
            const squareLabel = `${file}${rank}`;
            dict[squareLabel] = 0n; // Initialize with an empty bitboard
        });
    });

    return dict

}
export function storeAttack(activity, targetMask) {
    if (!activity) {
        Logger.error("storeAttack called with an invalid activity object:", activity);
        return false;
    }

    const targetSquare = bitmaskToSquare(targetMask)
    if (!targetSquare) {
        Logger.warn("storeAttack received an invalid targetMask:", targetMask);
        return false;
    }

    Logger.debug('Storing attack to', targetSquare, 'by', activity.current.piece)

    const attackDict = activity.current.isWhite ? activity.WhiteAttacks : activity.BlackAttacks
    attackDict[targetSquare].push(activity.current.piece)
}

export function storeMove(activity, move) {
    if (!activity) {
        Logger.error("storeMove called with an invalid activity object:", activity);
        return false;
    }

    Logger.debug('Storing move: ', move)

    const moveDict = activity.current.isWhite ? activity.WhiteMoves : activity.BlackMoves
    moveDict[move.source].push(move)

    activity.current.isWhite ? activity.WhiteMoveCount++ : activity.BlackMoveCount++

}

export function getDirection(sourceMask, targetMask) {
    if (sourceMask === 0n || targetMask === 0n) {
        Logger.warn("getDirection received an empty bitmask:", { sourceMask, targetMask });
        return null;
    }
    // Find the square indices of the source and target
    const sourceIndex = BigInt(Math.log2(Number(sourceMask)));
    const targetIndex = BigInt(Math.log2(Number(targetMask)));

    // Calculate the difference
    const diff = targetIndex - sourceIndex;

    // Normalize the difference for large distances
    if (diff % 9n === 0n) {
        return diff > 0n ? 9n : -9n; // Diagonal NE/SW
    }
    if (diff % 7n === 0n) {
        return diff > 0n ? 7n : -7n; // Diagonal NW/SE
    }

    if (diff % 8n === 0n) {
        return diff > 0n ? 8n : -8n; // Vertical
    }

    if (diff % 1n === 0n && abs(diff) < 8n) {
        return diff > 0n ? 1n : -1n; // Horizontal
    }

    Logger.warn("getDirection could not determine a valid direction for:", { sourceMask, targetMask });
    return null;
}

export function isCheck(activity) {
    if (!activity || !activity.bitboards) {
        Logger.error("isCheck called with an invalid activity object:", activity);
        return false;
    }

    let king = activity.current.isWhite ? activity.bitboards['K'] : activity.bitboards['k']

    if (!king) {
        Logger.debug("King not found in bitboards.");
        return false;
    }


    let kingSquare = bitmaskToSquare(king)
    let checks = activity.Checks[kingSquare]

    if (checks && checks.length > 0) {
        return checks
    }

    Logger.debug("King is safe on", kingSquare);
    return false;


}

export function clearsCheck(activity, targetMask) {

 
    const targetSquare = bitmaskToSquare(targetMask); 

    const pins = activity.Pins[activity.current.sourceSquare] || []
    let pinCount = pins?.length || 0

    const checks = isCheck(activity) || []
    let checkCount = checks?.length || 0

    Logger.debug('Checking checks for', activity.current.piece)


    if (checkCount === 0 && pinCount === 0) {


        Logger.debug(
            'No checks or pins, check cleared.'
        )

        return true

    } 

    for (let pin of pins) {
        if (pin.ray && (targetMask & pin.ray)) {

            Logger.debug(
                'Move is valid blocking move along pin trajectory, reducing pin count.'
            )

            pinCount--
        }
    }

    if (pinCount !== 0) {
        Logger.debug('Finished checking pins, move does not clear all pins, move invalid.')
        return false
    }

    if (checkCount === 0) {
        return true
    }

    for (let check of checks) {

        Logger.debug('Check', check)


        if (check.ray && (check.ray & targetMask)) {
            Logger.debug('Found move to block ray on', targetSquare)
            checkCount--
        }

        if (targetSquare === check.source) {
            Logger.debug('Found move to capture checking piece on:', check.source)
            checkCount--
        }
    }

    if (checkCount > 0) {
        Logger.debug('Still in check after move.')
        return false
    }

    return checkCount === 0

}

export function detectPins(activity) {
    let pinnedPieces = {};

    let kingMask = activity.isWhitePiece ? activity.bitboards["K"] : activity.bitboards["k"];
    let kingSquare = bitmaskToSquare(kingMask);

    const directions = {
        rook: [8n, -8n, 1n, -1n],   // N, S, E, W
        bishop: [9n, -9n, 7n, -7n], // NE, SW, NW, SE
        queen: [8n, -8n, 1n, -1n, 9n, -9n, 7n, -7n] // All sliding directions
    };

    // Loop through all sliding piece directions
    for (let [, dirSet] of Object.entries(directions)) {
        for (let direction of dirSet) {
            let ray = kingMask;
            let blockers = [];

            while (true) {
                ray = shiftBit(ray, direction);
                if (!ray) break; // Stop if off the board

                let squareMask = ray;
                let square = bitmaskToSquare(squareMask);

                if (squareMask & activity.bitboards.white || squareMask & activity.bitboards.black) {
                    blockers.push({ square, mask: squareMask });
                }

                if (blockers.length === 2) break; // More than one piece = not a pin
            }

            // If exactly one blocking piece is found, check if it's friendly
            if (blockers.length === 1) {
                let blocker = blockers[0];

                let isFriendly = activity.isWhitePiece
                    ? blocker.mask & activity.bitboards.white
                    : blocker.mask & activity.bitboards.black;

                let slidingAttackers = activity.isWhitePiece
                    ? activity.bitboards["r"] | activity.bitboards["b"] | activity.bitboards["q"]
                    : activity.bitboards["R"] | activity.bitboards["B"] | activity.bitboards["Q"];

                if (isFriendly && blocker.mask & slidingAttackers) {
                    pinnedPieces[blocker.square] = {
                        pinRay: generateRay(kingSquare, blocker.square),
                        direction: direction
                    };
                }
            }
        }
    }

    return pinnedPieces;
}

export function generateRay(sourceSquare, targetSquare) {
    let sourceMask = squareToBitmask(sourceSquare);
    let targetMask = squareToBitmask(targetSquare);

    let direction = getDirection(sourceMask, targetMask);
    if (!direction) return 0n; // Not in a straight line

    let ray = 0n;
    let current = sourceMask;

    while (true) {
        current = shiftBit(current, direction);
        if (current === 0n) break; // Out of bounds

        ray |= current; // Add square to the ray

        if (current === targetMask) break; // Stop at target square
    }

    return ray;
}

export function generate(pieces, activity, caseHandler) {
    for (let piece of pieces) {

        let maskArray = bitboardToMaskArray(activity.bitboards[piece])

        //skip removed pieces
        if (!maskArray || maskArray.length === 0) {
            Logger.debug('No maskArray for', piece)
            continue
        }

        Logger.debug('Preparing', piece, 'for', caseHandler.name )

        for (let mask of maskArray) {

            activity.current = {
                isWhite: piece === piece.toUpperCase(),
                piece: piece,
                sourceSquare: bitmaskToSquare(mask),
                sourceMask: mask
            }
            activity.current.directions = getDirections(activity)

            caseHandler(piece, activity)

            delete activity.current
        }
    }

}

function getDirections(activity) {
    const piece = activity.current.piece.toLowerCase() 

    switch (piece) {
        case 'n': return
        case 'p':
            return {
                capture: activity.current.isWhite ? [7n, 9n] : [-7n, -9n],
                move: activity.current.isWhite ? [8n, 16n] : [-8n, -16n]
            }

        case 'b':
            return [9n, 7n, -7n, -9n]; // NE, NW, SE, SW

        case 'r':
            return [8n, 1n, -1n, -8n]; // N, E, W, S

        case 'q':
            return [9n, 8n, 7n, 1n, -1n, -7n, -8n, -9n]; // NE, NW, SE, SW,  N, E, W, S

        case 'k':
            return [9n, 8n, 7n, 1n, -1n, -7n, -8n, -9n];
    }

    return 
}