import { bitboardToMaskArray, popcount } from "../../util/bitboards.js"
import { Logger } from "../../util/logger.js"

const PieceValueMap = new Map([
    ['p', 1],
    ['b', 3],
    ['n', 3],
    ['r', 5],
    ['q', 9],
    ['P', 1],
    ['B', 3],
    ['N', 3],
    ['R', 5],
    ['Q', 9]
])

export function scorePosition(bitboards) {

    let pieces = ['p', 'b', 'n', 'r', 'q', 'k', 'P', 'B', 'N', 'R', 'Q', 'K']
    let whiteScore = 0
    let blackScore = 0


    for (let piece of pieces) {

        const value = PieceValueMap.get(piece)

        let pieceMask = bitboards[piece]
        if (!pieceMask) {
            Logger.debug('No bitboard found for ', piece)
            continue
        }

        const pieceCount = popcount(pieceMask)

        let isWhite = piece === piece.toUpperCase()
        if (isWhite && value) {
            whiteScore += value * pieceCount
        }
        else if (value) {
            blackScore += value * pieceCount
        }

        

        let maskArray = bitboardToMaskArray(pieceMask)
        if (!maskArray || maskArray.length === 0) return new Error('No masks found for, ', piece)

        for (let mask of maskArray) {

            Logger.debug(mask)
            //evaluating position of pieces

            //evaluating attacks

            //evaluating defences

            //evaluating pins

            //evaluating discovered attacks

            //evaluating checks

            //evaluating checkmate
        }



    }


    if (whiteScore === 0) {
        whiteScore++
    }

    if (blackScore === 0) {
        blackScore++
    }


    // no dividing by 0
    if (whiteScore + blackScore === 0) return 0.5;

    // value between 0 and 1, where 0 is winning for white and 1 is winning for black
    return blackScore / (whiteScore + blackScore)
}
