import { Logger } from "../../util/logger.js"
import { generate, maskDict, moveDict } from "./activity.js"
import { storeKingAttacks, storeKingMoves } from "./king.js"
import { storeKnightMoves, storeKnightAttacks, precomputeKnightAttacks } from "./knight.js"
import { storePawnAttacks, storePawnMoves } from "./pawn.js"
import { storeRaycastAttacks, storeRaycastMoves } from "./raycasting.js"

export default class ActivityManager {

    constructor(bitboards) {
        this.newActivityManager(bitboards)

    }

    newActivityManager(bitboards) {
        this.generateActivity({ ...bitboards })

    }

    get() {
        return this.activity
    }

    get WhiteMoves() {
        return this.activity.WhiteMoves
    }

    get BlackMoves() {
        return this.activity.BlackMoves
    }

    get WhiteAttacks() {
        return this.activity.WhiteAttacks
    }
    get BlackAttacks() {
        return this.activity.BlackAttacks
    }
    get Checks() {
        return this.activity.Checks
    }
    get Discoverable() {
        return this.activity.Discoverable
    }

    get Pins() {
        return this.activity.Pins
    }

    get KnightAttacks() {
        return this.activity.KnightAttacks
    }

    get WhiteMoveCount() {
        return this.activity.WhiteMoveCount
    }

    get BlackMoveCount() {
        return this.activity.BlackMoveCount
    }

    generateActivity(gameState) {

        Logger.debug('Generating activity from gameState', gameState)

        this.clearActivity()

        this.activity.bitboards = { ...gameState.bitboards }
        this.activity.enPassant = gameState.enPassant
        this.activity.castling = gameState.castling

        let pieces = ['p', 'b', 'n', 'r', 'q', 'k', 'P', 'B', 'N', 'R', 'Q', 'K']

        //generate attacks
        generate(pieces, this.activity, this.#switchAttacks)
        //generate moves
        generate(pieces, this.activity, this.#switchMoves)

        Logger.debug('Generated new Activity:', this.activity)


    }

    clearActivity() {

        if (this.activity) {
            delete this.activity
        }

        this.activity = {}

        this.activity.WhiteAttacks = moveDict()
        this.activity.BlackAttacks = moveDict()
        this.activity.AttacksBySquare = maskDict()

        this.activity.Checks = moveDict()

        this.activity.WhiteMoves = moveDict()
        this.activity.BlackMoves = moveDict()

        this.activity.WhiteMoveCount = 0
        this.activity.BlackMoveCount = 0

        this.activity.Pins = moveDict()

        this.activity.Discoverable = moveDict()

        this.activity.KnightAttacks = precomputeKnightAttacks()

    }

    printActivity() {
        Logger.info(
            '\n Possible white moves: ',
            this.activity.WhiteMoveCount,
            this.activity.WhiteMoves,
            '\n Possible black moves: ',
            this.activity.BlackMoveCount,
            this.activity.BlackMoves,
            '\n Attacks (w/b): ',
            this.activity.WhiteAttacks,
            this.activity.BlackAttacks,
            '\n Checks: (w/b):',
            this.activity.Checks,
            '\n Pins: (w/b):',
            this.activity.Pins,
            '\n Discoverable attacks: (w/b):',
            this.activity.Discoverable,

        )
    }

    #switchAttacks(piece, activity) {
        switch (piece.toLowerCase()) {
            case 'p':
                storePawnAttacks(activity)
                break;

            case 'b':
            case 'r':
            case 'q':
                storeRaycastAttacks(activity)
                break;

            case 'n':
                storeKnightAttacks(activity)
                break;

            case 'k':
                storeKingAttacks(activity)
                break;
        }
    }

    #switchMoves(piece, activity) {
        switch (piece.toLowerCase()) {
            case 'p':
                storePawnMoves(activity)
                break;

            case 'b':
            case 'r':
            case 'q':
                storeRaycastMoves(activity)
                break;

            case 'n':
                storeKnightMoves(activity)
                break;

            case 'k':
                storeKingMoves(activity)
                break;
        }
    }


}




