import React, { useContext, useEffect, useRef } from "react";
import EvaluationPanel from "./EvaluationPanel.js";
import Board from "./Chessboard.js";
import InfoPanel from "./InfoPanel.js";
import { Logger } from "../../util/logger.js"
//import "../../styles/Chessboard.css"; // Styles for the bar
import { ChessContext } from "../../context/ChessProvider.js";
import PromotionModal from "./PromotionModal.js";

const GameUI = () => {
    const { gameState, aiThinking, uiStates, handlers } = useContext(ChessContext)
    const initialized = useRef(false);

    //On mount
    useEffect(() => {

        if (!initialized.current) {
            Logger.info("GameUI component initialised.");
            initialized.current = true
        } else {
            return
        }

    }, []);
    
    return (
 
         <div className="game-ui">
        
            {gameState && <EvaluationPanel/>}

            {<Board />}

            {aiThinking && (
                <div className="ai-thinking">
                    <div className="loader"></div>
                    <p>AI is thinking...</p>
                </div>
            )}

            {uiStates.showPromotionModal && <PromotionModal
                onSelect={handlers.handlePromotion}
            />}

            {gameState && <InfoPanel/>}

        </div>
    );
};

export default GameUI;