import React, { useContext } from "react";
import { ChessContext } from "../../context/ChessProvider.js";

const GameOverModal = () => {

    const { gameState, uiSetters } = useContext(ChessContext)

    return (
        <div className="game-over-modal">

            {gameState.gameOver}

            <button onClick={() => {
                uiSetters.setShowGameOverModal(false)
                uiSetters.setShowGameUI(false)
                uiSetters.setShowLandingMenu(true)
            }}>
                Main Menu
            </button>

        </div>
    );
};

export default GameOverModal;