import { bitmaskToSquare, squareToBitmask, bitmaskToPiece, bitboardToMaskArray, bitboardToString } from "../../util/bitboards.js";
import { Logger } from "../../util/logger.js";
import { clearsCheck, storeAttack, storeMove } from "./activity.js";

const FILE_A_MASK = 0x0101010101010101n; // File A
const FILE_H_MASK = 0x8080808080808080n; // File H

export function precomputeKnightAttacks() {
    const attacks = {}; // Use square labels as keys
    const files = ["a", "b", "c", "d", "e", "f", "g", "h"];
    const ranks = ["1", "2", "3", "4", "5", "6", "7", "8"];
    const squareLabels = [];

    for (const rank of ranks) {
        for (const file of files) {
            squareLabels.push(file + rank);
        }
    }

    //console.log('Computing knight attacks!')

    for (const square of squareLabels) {
        const squareMask = squareToBitmask(square); // Convert to bitmask

        // Generate all possible knight moves
        let attackMask = 0n;
        attackMask |= (squareMask << 17n) & ~FILE_A_MASK; // Up 2, Left 1
        attackMask |= (squareMask << 15n) & ~FILE_H_MASK; // Up 2, Right 1
        attackMask |= (squareMask << 10n) & ~(FILE_A_MASK | (FILE_A_MASK << 1n)); // Up 1, Left 2
        attackMask |= (squareMask << 6n) & ~(FILE_H_MASK | (FILE_H_MASK >> 1n)); // Up 1, Right 2
        attackMask |= (squareMask >> 6n) & ~(FILE_A_MASK | (FILE_A_MASK << 1n)); // Down 1, Left 2
        attackMask |= (squareMask >> 10n) & ~(FILE_H_MASK | (FILE_H_MASK >> 1n)); // Down 1, Right 2
        attackMask |= (squareMask >> 15n) & ~FILE_A_MASK; // Down 2, Left 1
        attackMask |= (squareMask >> 17n) & ~FILE_H_MASK; // Down 2, Right 1

        attacks[square] = attackMask;
        //Logger.debug('Knight Mask: ', attackMask)
    }
    return attacks;
}

export function storeKnightAttacks(activity) {

    const attackMask = activity.KnightAttacks[activity.current.sourceSquare]

    for (let targetMask of bitboardToMaskArray(attackMask)) {
        storeAttack(activity, targetMask)

        let targetSquare = bitmaskToSquare(targetMask)
        let opponentKing = activity.current.isWhite === true
            ? activity.bitboards['k']
            : activity.bitboards['K']

        if ((targetMask & opponentKing)) {
            activity.Checks[targetSquare].push({
                piece: activity.current.piece,
                source: activity.current.sourceSquare,
                target: targetSquare
            })
        }

    }

}
export function storeKnightMoves(activity) {

    const attackMask = activity.KnightAttacks[activity.current.sourceSquare]

    if (attackMask !== 0n) {

        for (let targetMask of bitboardToMaskArray(attackMask)) {

            let targetPiece = bitmaskToPiece(targetMask, activity.bitboards)
            let targetSquare = bitmaskToSquare(targetMask)

            let move = {
                source: activity.current.sourceSquare,
                target: targetSquare,
                piece: activity.current.piece
            }

            if (targetPiece) {
                //skip friendly fire targets
                if (activity.current.isWhite && targetPiece === targetPiece.toUpperCase()) continue
                if (!activity.current.isWhite && targetPiece === targetPiece.toLowerCase()) continue

                move.capture = targetPiece
            }

            if (clearsCheck(activity, targetMask)) {
                storeMove(activity, move)
            }
            
        }
    }
}