import { initializeBitboards } from './bitboards.js'
import { Logger } from './logger.js';
export function parseFEN(fen) {
    const [position, turn, castling, enPassant, halfMove, fullMove] = fen.split(" ");

    const bitboards = initializeBitboards();
    const rows = position.split("/").reverse(); // Bottom row is rank 1
    rows.forEach((row, rank) => {
        let file = 0;
        for (const piece of row) {
            if (!isNaN(piece)) {
                // Empty squares
                file += parseInt(piece, 10);
            } else {
                const squareIndex = BigInt(rank * 8 + file);
                bitboards[piece] |= 1n << squareIndex;

                if (piece === piece.toUpperCase()) {


                    bitboards['white'] |= 1n << squareIndex;
                } else {
                    bitboards['black'] |= 1n << squareIndex;
                }

                file++;
            }
        }
    });

    return {
        bitboards, // Bitboards for piece locations
        turn, // 'w' or 'b'
        castling, // Castling rights string like 'KQkq'
        enPassant: enPassant === "-" ? null : enPassant, // Square or null
        halfMove: parseInt(halfMove, 10), // Half-move clock to track 50 'plies' where no pawn is moved or piece taken
        fullMove: parseInt(fullMove, 10), // Full-moves are pairs of white/black moves and response
        FEN: fen
    };
}

// Converts structure back to FEN string
export function parseGameState (gameState) {
    const { bitboards, turn, castling, enPassant, halfMove, fullMove } = { ...gameState };

    delete bitboards['white']
    delete bitboards['black']

    let position = "";
    for (let rank = 7; rank >= 0; rank--) {
        let emptySquares = 0;
        for (let file = 0; file < 8; file++) {
            const squareIndex = BigInt(rank * 8 + file);
            const piece = Object.keys(bitboards).find(
                (key) => (bitboards[key] & (1n << squareIndex)) !== 0n
            );

            if (piece) {
                if (emptySquares > 0) {
                    position += emptySquares;
                    emptySquares = 0;
                }
                position += piece;
            } else {
                emptySquares++;
            }
        }
        if (emptySquares > 0) position += emptySquares;
        if (rank > 0) position += "/";
    }
 
    let FEN = [
        position,
        turn,
        castling || "-",
        enPassant || "-",
        halfMove,
        fullMove,
    ].join(" ");

    print(FEN)

    return FEN;
}

export function print(fen) {

    const boardArray = [];
    const [position,,, enPassant] = fen.split(" ");
    const rows = position.split("/");

    for (const row of rows) {
        const expandedRow = [];
        for (const char of row) {
            if (isNaN(char)) {
                expandedRow.push(char); // Push piece directly
            } else {
                // Push empty squares
                expandedRow.push(...Array(parseInt(char, 10)).fill("."));
            }
        }
        boardArray.push(expandedRow);
    }

    if (enPassant && enPassant !== "-") {
        const file = enPassant.charCodeAt(0) - "a".charCodeAt(0);
        const rank = 8 - parseInt(enPassant[1], 10); 
        boardArray[rank][file] = "e";

    }

    let print = '\n'
    boardArray.forEach((row) => print += row.join(" ") + '\n');
    //Logger.debug(print);


};