import { getDirection } from "../classes/activity/activity.js";
import { Logger } from "./logger.js";

export const FILE_A_MASK = 0x0101010101010101n; // File A (left edge)
export const FILE_H_MASK = 0x8080808080808080n; // File H (right edge)

export const initializeBitboards = () => ({
    P: 0n, // White pawns
    N: 0n, // White knights
    B: 0n, // White bishops
    R: 0n, // White rooks
    Q: 0n, // White queens
    K: 0n, // White king
    p: 0n, // Black pawns
    n: 0n, // Black knights
    b: 0n, // Black bishops
    r: 0n, // Black rooks
    q: 0n, // Black queens
    k: 0n, // Black king
    white: 0n, //All White pieces
    black: 0n  //All Black pieces
});

export function bitmaskToPiece(bitmask, bitboards) {

    if (!bitboards || typeof bitboards !== "object") {
        Logger.error("bitmaskToPiece received an invalid bitboards object:", bitboards);
        return null;
    }


    for (const piece in bitboards) {

        if(piece === 'white' || piece === 'black') continue

        if (bitboards[piece] & bitmask) {

            return piece
        }
    }

    return null
}

export function squareToBitmask(square) {
    if (!square || square.length !== 2 || typeof square !== typeof 'string') {
        throw new Error("Invalid square format for", typeof square);
    }


    const file = square.charCodeAt(0) - "a".charCodeAt(0); // File: 'a' = 0, 'b' = 1, ..., 'h' = 7
    const rank = parseInt(square[1], 10) - 1; // Rank: '1' = 0, '2' = 1, ..., '8' = 7

    if (file < 0 || file > 7 || rank < 0 || rank > 7) {
        throw new Error("Square out of range");
    }

    return 1n << BigInt(rank * 8 + file); // Calculate bitmask
}

export function bitmaskToSquare (bitmask) {
    if (typeof bitmask !== "bigint" || bitmask === 0n) {
        Logger.warn("Invalid bitmask input for bitmaskToSquare:", bitmask);
        return null;
    }

    let squareIndex = 0
    while (bitmask > 1n) {
        bitmask >>= 1n;
        squareIndex++;
    }
    const file = String.fromCharCode(97 + (squareIndex % 8)); // Convert 0-7 to 'a'-'h'
    const rank = Math.floor(squareIndex / 8) + 1; // Convert 0-7 to 1-8 (rank numbers)

    return file + rank;
}

export function bitboardToString(bitboard) {

    if (typeof bitboard !== "bigint") {
        Logger.err("Invalid bitboard input for bitboardToString:", bitboard);
        return "Invalid bitboard input";
    }

    let boardStr = '';

    // Loop through each rank (8 rows)
    for (let rank = 7; rank >= 0; rank--) {
        let row = '';

        // Loop through each file (8 columns)
        for (let file = 0; file < 8; file++) {
            const squareIndex = rank * 8 + file; // Calculate the bit index (0 to 63)
            const bit = (bitboard >> BigInt(squareIndex)) & 1n; // Extract the bit for the square

            if (bit === 1n) {
                row += 'X '; // X represents the presence of a piece on that square (you can change it to 'P', 'K', etc.)
            } else {
                row += '. '; // Empty square
            }
        }

        // Append the row to the board string
        boardStr += row.trim() + '\n';
    }

    // Print the board to the console
    return boardStr;
}

export function bitboardToMaskArray(bitboard) {

    if (typeof bitboard !== "bigint") {
        Logger.warn("bitboardToMaskArray received a non-BigInt value:", bitboard);
        return [];
    }


    return Array.from({ length: 64 }, (_, index) => 1n << BigInt(index))
        .filter((mask) => (bitboard & mask) !== 0n);

}

export function abs(bigint) {
    return bigint < 0n ? -bigint : bigint
}

export function popcount(n) {
    let count = 0;
    while (n) {
        count++;
        n &= (n - 1n); // Removes the lowest set bit
    }
    return count;
}

export function generateRandomMagic() {
    return (
        (BigInt(Math.floor(Math.random() * 0xFFFFFFF)) << 32n) |
        BigInt(Math.floor(Math.random() * 0xFFFFFFF)) & 0xFFFFFFFFFFFFFFFEn
    );
}

export function generateAttackMask(sourceSquare, directions) {
    let attackMask = 0n;
    let bitboard = squareToBitmask(sourceSquare); // Convert square to bitboard form

    for (let direction of directions) {
        let ray = bitboard;
        while (true) {
            ray = shiftBit(ray, direction); // Shift in the given direction
            if (!ray) break; // Stop if off the board
            attackMask |= ray;
        }
    }

    return attackMask;
}

export function shiftBit(sourceMask, direction) {
    if (!sourceMask) return 0n;

    let shifted = direction > 0n ? (sourceMask << direction) : (sourceMask >> -direction);
    if (getDirection(sourceMask, shifted) !== direction) {
        return 0n;
    }

    if ((sourceMask & FILE_A_MASK) && (shifted & FILE_H_MASK)) { // Prevent left wrap
        Logger.debug('Prevented left wrap from', bitmaskToSquare(sourceMask))
        return 0n;
    }

    if ((sourceMask & FILE_H_MASK) && (shifted & FILE_A_MASK)) { // Prevent right wrap
        Logger.debug('Prevented left wrap from', bitmaskToSquare(sourceMask))
        return 0n;

    }

    // Ensure the shift doesn't move off the board
    if (shifted === 0n || (shifted & ~(0xFFFFFFFFFFFFFFFFn))) return 0n;

    return shifted;
}


export function findMagicNumber(square, attackMask) {
    const blockerPermutations = generateBlockerPermutations(attackMask);

    for (let attempt = 0; attempt < 1000000; attempt++) {
        let magic = generateRandomMagic(); // Generate a candidate magic number
        let used = new Set();
        let success = true;

        for (let blockers of blockerPermutations) {
            let index = (blockers * magic) >> (64n - BigInt(blockerPermutations.length));
            if (used.has(index)) {
                success = false;
                break;
            }
            used.add(index);
        }

        if (success) return magic;
    }

    throw new Error("Failed to find magic number");
}


export function generateBlockerPermutations(attackMask) {
    let blockers = [];
    let subset = 0n;

    do {
        blockers.push(subset);
        subset = (subset - attackMask) & attackMask; // Generate next subset
    } while (subset);

    return blockers;
}

