import { Logger } from "./logger.js";
class EventEmitter {
    constructor() {
        this.listeners = {};
        this.eventQueue = [];
        this.isReady = false;
    }

    on(event, listener) {
        if (!this.listeners[event]) {

            Logger.debug(
                'Listener registering new event type:',
                event
            )

            this.listeners[event] = [];
        }

        Logger.debug(
            'Registering listener for:',
            event
        )

        this.listeners[event].push(listener);

        // If the event queue has pending events, process them
        if (this.isReady) {
            this.processQueue();
        }
    }

    emit(event, payload) {
        if (this.isReady) {
            this.invokeListeners(event, payload);
        } else {
            this.eventQueue.push({ event, payload }); // Add to queue if not ready
        }
    }

    setReady() {
        this.isReady = true;
        this.processQueue(); // Process all queued events
    }

    processQueue() {

        Logger.debug('Processing Event Queue...')

        while (this.eventQueue.length > 0) {
            const { event, payload } = this.eventQueue.shift();
            this.invokeListeners(event, payload);
        }
    }

    invokeListeners(event, payload) {

        Logger.debug(
            'Listeners: ',
            this.listeners
        )

        Logger.debug(
            'EventQueue: ',
            this.eventQueue
        )

        if (this.listeners[event]) {

            Logger.info(
                'Queue delivering ', 
                event,
                'payload',
                payload
            )

            this.listeners[event].forEach((listener) => listener(payload));
        }
    }
}

const Events = new EventEmitter

export default Events;
