import React, { useContext } from "react";
import "../../styles/Chessboard.css"; // Styles for the bar
import { ChessContext } from "../../context/ChessProvider.js";

const EvaluationBar = () => {
    const { gameState } = useContext(ChessContext)

    return (
        <div className="evaluation-bar">

             <div
                className="black-bar"
                style={{
                    height: `${(Math.min(Math.max(0, gameState.evaluation), 1)) * 100}%`,
                }}
            ></div>
            <div
                className="white-bar"
                style={{
                    height: `${Math.min(Math.max(1, gameState.evaluation), 0) * 100}%`,
                }}
            ></div>

        </div>
    );
};

export default EvaluationBar;
