import React, { useContext } from "react";
import { ChessContext } from "../../context/ChessProvider.js";

const LandingMenu = () => {

    const { uiSetters } = useContext(ChessContext)

    return (
        <div className="landing-menu">
            <button onClick={() => {
                uiSetters.setShowPlayMenu(true)
                uiSetters.setShowLandingMenu(false)
            }}>
                Play
            </button>
            <button onClick={() => {
                window.open("https://github.com/seanleekinahan/portfolio-website", "_blank", "noopener,noreferrer");
            }}>
                View Source Code
            </button>
        </div>
    );
};

export default LandingMenu;