import {  bitboardToMaskArray, bitboardToString, bitmaskToPiece, bitmaskToSquare, shiftBit } from "../../util/bitboards.js";
import { Logger } from "../../util/logger.js";
import { clearsCheck, storeMove, storeAttack } from "./activity.js";



export const FILE_A_MASK = 0x0101010101010101n; // File A (left edge)
export const FILE_H_MASK = 0x8080808080808080n; // File H (right edge)
export const RANK_1_MASK = 0x00000000000000FFn; // Rank 1 (bottom edge)
export const RANK_8_MASK = 0xFF00000000000000n; // Rank 8 (top edge)

export const edgeMaskMap = new Map([
    [9n, FILE_H_MASK | RANK_8_MASK], // North-East
    [7n, FILE_A_MASK | RANK_8_MASK], // North-West
    [-7n, FILE_H_MASK | RANK_1_MASK], // South-East
    [-9n, FILE_A_MASK | RANK_1_MASK], // South-West
    [8n, RANK_8_MASK],                // North
    [-8n, RANK_1_MASK],                // South
    [1n, FILE_H_MASK],                // East
    [-1n, FILE_A_MASK],                // West
]);

function generateRay(sourceMask, direction, bitboards) {
    if (typeof sourceMask !== "bigint" || typeof direction !== "bigint") {
        Logger.warn("Invalid input for generateRay:", { sourceMask, direction });
        return 0n;
    }

    let ray = 0n; 
    let current = sourceMask; 
    let edgeMask = edgeMaskMap.get(direction)
    if (!edgeMask) {
        Logger.warn('No edge mask found with direction: ', direction)
        return 0n
    } 

    //boundary conditions: while bitboard 'not empty' and 'not full'
    while (current > 0n && current < 0xFFFFFFFFFFFFFFFFn) {

        current = shiftBit(current, direction)

        if (current & edgeMask) {

            ray |= current;
            break
        };

        if ((current & bitboards['white']) ||
            (current & bitboards['black'])) {

            ray |= current;
            break;
        }

        ray |= current;

    }

    return ray;
}

export function storeRaycastAttacks(activity) {
    if (!activity) {
        Logger.error("storeAttacks called with an invalid activity object:", activity);
        return false;
    }

    let attackMask = 0n
    for (let direction of activity.current.directions) {

        let ray = generateRay(activity.current.sourceMask, direction, activity.bitboards)
        if (ray === 0n) continue

        for (let targetMask of bitboardToMaskArray(ray)) {
            storeAttack(activity, targetMask)
            attackMask |= targetMask

            let targetSquare = bitmaskToSquare(targetMask)
            let opponentKing = activity.current.isWhite === true
                ? activity.bitboards['k']
                : activity.bitboards['K']

            if ((targetMask & opponentKing)) {
                activity.Checks[targetSquare].push({
                    piece: activity.current.piece,
                    source: activity.current.sourceSquare,
                    target: targetSquare,
                    ray: ray
                })
            }
        }

    }

    activity.AttacksBySquare[activity.current.sourceSquare] = attackMask
    Logger.debug('Combined attack mask for', activity.current.piece, '\n',
        bitboardToString(activity.AttacksBySquare[activity.current.sourceSquare]))
}

export function storeRaycastMoves(activity) {
    if (!activity || !activity.current.sourceMask) {
        Logger.error("Invalid input to raycastActivity:", { activity });
        return;
    }

    const sourceSquare = bitmaskToSquare(activity.current.sourceMask)

    for (let direction of activity.current.directions) {

        let ray = generateRay(activity.current.sourceMask, direction, activity.bitboards)
        if (ray === 0n) continue

        Logger.debug('Checking ray for ', activity.current.piece,bitboardToString(ray))

        //checking each position in ray, storing all as attacks, avoiding friendly fire for moves
        for (let targetMask of bitboardToMaskArray(ray)) {
            const targetSquare = bitmaskToSquare(targetMask)

            let move = {
                source: sourceSquare,
                target: targetSquare,
                piece: activity.current.piece,
            }



            let targetPiece = bitmaskToPiece(targetMask, activity.bitboards)
            //skip friendly fire targets for movement
                if (targetPiece) {

                    move.capture = targetPiece

                    raycastOverPiece(activity, targetPiece, targetMask, direction)

                if ((activity.current.isWhite && targetPiece === targetPiece.toUpperCase()) ||
                    (!activity.current.isWhite && targetPiece === targetPiece.toLowerCase())) {
                    continue
                }

            }


            if (clearsCheck(activity, targetMask)) {
                storeMove(activity, move)
            } 

        }
    }



}

function raycastOverPiece(activity, pivotPiece, pivotMask, direction) {

    let ray = generateRay(pivotMask, direction, activity.bitboards)
    if (ray === 0n) return

    for (let targetMask of bitboardToMaskArray(ray)) {

        let targetPiece = bitmaskToPiece(targetMask, activity.bitboards)

        //looking at first target piece found after hopped piece
        if (!targetPiece) continue
        if (targetPiece) {

            //if the hopped piece is friendly, return for now - handle discovered attacks later
            if ((activity.current.isWhite && pivotPiece === pivotPiece.toUpperCase()) ||
                (!activity.current.isWhite && pivotPiece === pivotPiece.toLowerCase())) {
                    return
            }

            //if the target piece isn't friendly
            if ((activity.current.isWhite && targetPiece === 'k' ||
                (!activity.current.isWhite && targetPiece === 'K'))) {

                activity.Pins[bitmaskToSquare(pivotMask)].push(
                    {
                        piece: pivotPiece,
                        pinnedTo: targetPiece,
                        pinnedBy: activity.current.piece,
                        ray: ray
                    }
                )

                Logger.debug('Found', pivotPiece, bitmaskToSquare(pivotMask), 'pinned to', targetPiece, 'by', activity.current.piece)
            }

            //only looking at first piece found
            return

        }


    }
}
