window.$ = $

import React, { useEffect, useRef, useState, useContext, useLayoutEffect } from "react";
import Chessboard from "chessboardjs"; // Import chessboard.js
import '@chrisoakman/chessboardjs/dist/chessboard-1.0.0.css';
import $ from "jquery"; // Chessboard.js requires jQuery
import { Logger } from '../../util/logger.js'
import { ChessContext } from "../../context/ChessProvider.js";

const Board = () => {

    const { gameState, ChessRef, handlers, uiSetters } = useContext(ChessContext)
    const [boardKey, setBoardKey] = useState(0)

    const boardInstance = useRef(null)
    const boardRef = useRef(null)

    const mounted = useRef(false);
    const mountedDOM = useRef(false);

    const mountBoard = () => {

        let boardConfig = {
            draggable: true,
            onDragStart: drag,
            onDrop: drop,
            sparePieces: false,
            pieceTheme: 'img/chesspieces/{piece}.png',
            moveSpeed: 'fast',
            snapbackSpeed: 0,
            snapSpeed: 0,
        }

        boardInstance.current = Chessboard(boardRef.current, boardConfig);
        boardInstance.current.resize();

        setBoardKey(1)

        Logger.info('Mounted board instance to boardRef: ', boardInstance.current)

    }

    const drag = (source, piece) => {

        if (ChessRef.current.gameState.playerColour !== ChessRef.current.gameState.turn) {
            return false
        }

        //stops player dragging wrong piece
        let search = ChessRef.current.gameState.turn === 'w' ? piece.search(/^w/) : piece.search(/^b/)
        if (search !== 0) {
            return false
        }

        let moves = ChessRef.current.getMovesFromSquare(source)
        let indicatorColour = ChessRef.current.gameState.turn === 'w' ? 'white' : 'black'
        moves.forEach(move => {
            let targetSquare = document.querySelector(`[data-square="${move.target}"]`);

            if (targetSquare) {
                let indicator = document.createElement("div");
                let pieceImage = targetSquare.querySelector("img"); // Get piece inside the square

                if (move.capture && pieceImage) {
                    pieceImage.classList.add("capture-highlight");

                } else {
                    indicator.classList.add("move-indicator");
                    indicator.style.backgroundColor = indicatorColour;

                    targetSquare.appendChild(indicator); // Add the circle inside the square
                }


            }
        });

        return
    }

    const drop = (source, target) => {
        const move = { source, target}

        if (move.target === 'offboard') {
            return
        }

        document.querySelectorAll(".move-indicator").forEach(indicator => {
            indicator.remove(); // Remove all move indicators after dragging
        });

        document.querySelectorAll(".capture-highlight").forEach(square => {
            square.classList.remove("capture-highlight");
        });

        handlers.handleMove(move)

        return
    }

    //Update Chessboard.js position with gameState.FEN
    useEffect(() => {

        if (boardInstance.current && gameState) {

            

            if (gameState.playerColour === 'b' && !boardInstance.current.flipped) {
                Logger.debug('Flipping board once')
                boardInstance.current.flip()
                boardInstance.current.flipped = true
            }

            Logger.debug('Updating board.position')
            boardInstance.current.position(gameState.FEN);

            Logger.info(
                'GameState set in Chessboard.js: ', gameState
            )

            if (gameState.promotion && gameState.turn === gameState.playerColour) {
                uiSetters.setShowPromotionModal(true)
                return
            }

            if (gameState.gameOver) {
                uiSetters.setShowGameOverModal(true)
                return
            }


        }

    }, [gameState]);

    useLayoutEffect(() => {

        if (!mounted.current) {
            mounted.current = true
        }

        if (!mountedDOM.current && boardRef.current) {

            if (!boardInstance.current && boardKey === 0) {
                Logger.debug('BoardKey', boardKey)

                mountBoard()

                Logger.info('Preventing double mount of board.')
                mountedDOM.current = true

                return
            }
        }

        // Cleanup on unmount
        return () => {
            setTimeout(() => {
                if (boardInstance.current) {
                    boardInstance.current.destroy();
                    boardInstance.current = null;
                    setBoardKey(0)
                    Logger.info("Previous board instance destroyed.");
                }
            }, 100);
        };

    }, []);


    return (

        <div
            className={"chessboard-panel"}
            ref={boardRef}
            id={boardRef.current}
        >
        </div>
        
    )

};

export default Board;
