
import { bitmaskToPiece, squareToBitmask } from "../../util/bitboards.js";
import { parseFEN, parseGameState } from "../../util/fen.js";
import { Logger } from "../../util/logger.js";
import { scorePosition } from "../evaluator/index.js";

export default class GameState {

    constructor(id, FEN) {
        this.id = id;

        Logger.debug('Creating new GameState instance: ', this.id)

        Object.assign(this, parseFEN(FEN));

        //TODO:
        this.PGN = 'WIP' //implement pgn generation
        this.displayText = 'WIP' //implement flavour text generation based on position
        this.turnCount = 0


    }

    movePiece(move) {

        if (!move || !move.source || !move.target) {
            throw new Error("Invalid move object provided.");
        }

        try {
            Logger.debug('Processing move: ', move)

            this.source = this.#lookAtSquare(move.source)
            this.target = this.#lookAtSquare(move.target)

            //take a copy of enPassant
            const enPassant = this.enPassant
            if (move.enPassant) {
                this.enPassant = move.enPassant
            }
            this.#updateEnPassant(enPassant)


            //moving piece and capturing any target piece
            const targetPiece = bitmaskToPiece(this.target.bitmask, this.bitboards)
            this.#removePieceFromBitboard(this.source.piece, this.source.bitmask)
            this.#addPieceToBitboard(this.source.piece, this.target.bitmask)
            if (targetPiece) {
                this.#removePieceFromBitboard(targetPiece, this.target.bitmask)
            }


            //updating castling rights
            this.#updateCastlingRights(move, this.source.piece)

            //updating FEN string with new position and turn information, ensuring continuity
            this.FEN = parseGameState(this)
            Object.assign(this, parseFEN(this.FEN))


            if (move.promotion) {

                Logger.debug('Setting promotion on ', move.promotion)

                this.promotion = move.promotion
                return this.turn === this.playerColour ? 'promotion' : 'engine-promotion'
            } else {
                delete this.promotion
            }


            return true
        } catch (error) {
            Logger.error('Error in movePiece', error)
            return false
        }


    }

    updateTurn() {
        //updating turn information
        this.halfMove = this.target.piece || this.source.piece.toLowerCase() === "p" ? 0 : this.halfMove + 1;
        if (this.turn === "b") this.fullMove++;
        this.turn = this.turn == "w" ? "b" : "w";
        this.turnCount++

        //updating FEN string with new position and turn information, ensuring continuity
        this.FEN = parseGameState(this)
        this.evaluation = scorePosition(this.bitboards)
        Object.assign(this, parseFEN(this.FEN))
    }

    #addPieceToBitboard(piece, bitmask) {
        if (!piece || typeof piece !== "string") {
            throw new Error("Invalid piece provided to #addPieceToBitboard.");
        }
        if (typeof bitmask !== "bigint") {
            throw new Error("Invalid bitmask provided to #addPieceToBitboard.");
        }

        Logger.debug('Adding piece to bitboard: ', piece)
        this.bitboards[piece] |= bitmask
    }

    #removePieceFromBitboard(piece, bitmask) {
        if (!piece || typeof piece !== "string") {
            throw new Error("Invalid piece provided to #removePieceFromBitboard.");
        }
        if (typeof bitmask !== "bigint") {
            throw new Error("Invalid bitmask provided to #removePieceFromBitboard.");
        }

        Logger.debug('Removing piece from bitboard: ', piece)
        this.bitboards[piece] &= ~bitmask
    }

    #updateCastlingRights(move, piece) {

        if (move.castlingRook) {
            let rook = this.turn === 'w' ? 'R' : 'r'
            this.#removePieceFromBitboard(rook, squareToBitmask(move.castlingRook.source))
            this.#addPieceToBitboard(rook, squareToBitmask(move.castlingRook.target))
        }

        const kingMoved = piece.toLowerCase() === "k";
        const rookMoved = piece.toLowerCase() === "r";
        if (kingMoved) {
            Logger.debug('Stopped castling for', piece)
            this.castling = this.castling.replace(this.turn === "w" ? /[KQ]/g : /[kq]/g, "");
        }
        if (rookMoved) {
            const rookStartPositions = this.turn === "w" ? [0, 7] : [56, 63];
            Logger.debug('Stopped castling for', piece, rookStartPositions[0])

            if (move.source === 'a1') this.castling = this.castling.replace('Q', "");//Q
            if (move.source === 'h1') this.castling = this.castling.replace('K', ""); //K
            if (move.source === 'a8') this.castling = this.castling.replace('q', "");//q
            if (move.source === 'h8') this.castling = this.castling.replace('K', "");//k
         
        }

        Logger.debug('Confirming gameState.castling after update', this.castling)
    }

    #updateEnPassant(previousEnPassant) {


        if (!this.enPassant) return

        //check for active en passant rights
        if (this.enPassant !== '-' &&
            (this.source.piece === 'P' || this.source.piece === 'p')) {

            //Check if current move targets en passant square
            const enPassantSquare = this.#lookAtSquare(this.enPassant)
            if (enPassantSquare && enPassantSquare.bitmask === this.target.bitmask) {

                const direction = this.turn === "w" ? 8n : -8n;
                const capturedPiece = this.turn === "w" ? 'p' : 'P'
                const captureMask = this.target.bitmask << -direction

                this.#removePieceFromBitboard(capturedPiece, captureMask)
                this.enPassant = '-'
                delete this.enPassantMask
            }

        }

        //Check for stale en passant flags
        if (previousEnPassant !== '-' && previousEnPassant === this.enPassant) {
            //Logger.debug('Removing stale en passant.')
            this.enPassant = '-'
            delete this.enPassantMask
        }

        //Logger.debug('En Passant (post-update): ', this.gameState.enPassant)
    }

    #lookAtSquare(lookAt) {

        Logger.debug('Looking at ', lookAt, typeof lookAt)

        const square = {
            label: lookAt,
            piece: '',
            bitmask: 0n,
        }

        square.bitmask = squareToBitmask(lookAt)
        square.piece = bitmaskToPiece(square.bitmask, this.bitboards)
        return square

    }

    promotePawn(piece) {
        if (!piece || typeof piece !== "string") {
            throw new Error("Invalid piece string provided for promotion.");
        }

        if (!this.promotion) {
            throw new Error("No promotion square set in GameState.");
        }

        const isWhite = piece === piece.toUpperCase()
        const pawn = isWhite === true ? 'P' : 'p'

        this.#addPieceToBitboard(piece, squareToBitmask(this.promotion))
        this.#removePieceFromBitboard(pawn, squareToBitmask(this.promotion))

        delete this.promotion

        this.updateTurn()
    }


}
